.icon {
  display: block;
  width: 24px;
  height: 24px;
  background-size: cover;
  transition: transform 0.3s;

  &:hover {
    transform: scale(1.2);
  }

  &--phone {
    background-image: url(../icons/icon-phone.svg);

    @media (min-width: $desktop-min-width) {
      &:hover::before {
        content: '+1 234 5555-55-55';
        padding-right: 20px;
        position: absolute;
        top: 10px;
        left: -160px;
        color: $main-color-light;
        font-size: 12px;
        font-weight: 700;
        line-height: 16px;
        letter-spacing: 2px;
      }
    }
  }

  &--menu {
    background-image: url(../icons/icon-burger-menu.svg);

    &:hover {
      background-image: url(../icons/icon-burger-menu-hover.svg);
    }
  }

  &--close {
    background-image: url(../icons/icon-close.svg);
  }

  @media (min-width: $tablet-min-width) {
    width: 32px;
    height: 32px;
  }
}
