.btn {
  background-color: $main-color-light;
  display: block;
  padding: 14px 48px;
  border-radius: 40px;
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
  border: none;
  cursor: pointer;

  &:hover {
    box-shadow: 1px 4px 16px 0 #E8E8F066;
  }

  &:active {
    box-shadow: 1px 4px 16px 0 #E8E8F0A3;
  }
}
